const defaultTranslations = {
  PL: {
    ERROR_100: "Nie znaleziono",
    ERROR_101: "Niezidentyfikowany bd",
    ERROR_102: "Zbir ju istnieje",
    ERROR_103: "Bdna nazwa uytkownika lub haso",
    ERROR_104: "Ksika z takim samym ISBN ju istnieje",
    ERROR_105: "Brak dostpu",
    ERROR_106: "Brak odpowiednich zasobw"
  }
};

class i18n {
  constructor(lang = "PL", translations = defaultTranslations) {
    this.lang = lang;
    this.translations = translations;

    this.t = this.translation;
  }

  // Simple translation without variety
  translation(id, lang = this.lang) {
    return this.translations[lang][id];
  }
}

export default i18n;



